import React, { useState } from "react";


function Formtext(props) {
 
  const [change, setchange] = useState("Enter");
  const changebtn = (e) => {
    const text = e.target.value;
    setchange(text);
   
  };
  const uppercase = () => {
    setchange(change.toUpperCase());
    props.showAlert("converted to uppercase","success")

  };
  const lowercase = () => {
    setchange(change.toLowerCase());
    props.showAlert("converted to uppercase","success")
  };
  const deletetext = () => {
    setchange(" ");
    props.showAlert("converted to uppercase","danger")
  };
  const copytext = () => {
   
    navigator.clipboard.writeText(change);
    props.showAlert("converted to uppercase","success")
  };
  const extratext = () => {
    const newtext = change.split(/[ ]+/);
    setchange(newtext.join(""));
    props.showAlert("converted to uppercase","success")
  };

  return (
    <div className="container">
      <h1 className={`text-${props.mode === "light" ? "dark" : "light"} mx-2`}>
        {props.heading}
      </h1>
      <div className="mb-3">
        <label for="exampleFormControlTextarea1" className="form-label"></label>
        <textarea
          style={{
            backgroundColor: props.mode === "dark" ? "gray" : "white",
            color: props.mode === "dark" ? "white" : "black",
          }}
          id="text"
          placeholder="Enter your text"
          className="form-control  "
          id="exampleFormControlTextarea1"
          rows="8"
          name="text"
          value={change}
          onChange={changebtn}
        ></textarea>
      </div>
      <button className={`btn btn-primary mx-1 ${change.length > 0 ? " " : "disabled "}`} onClick={uppercase}>
        {" "}
        convert to appercase{" "}
      </button>
      <button className={`btn btn-primary mx-1 ${change.length > 0 ? " " : "disabled "}`} onClick={lowercase}>
        {" "}
        convert to lowercase{" "}
      </button>
      <button  className={`btn btn-primary mx-1 ${change.length > 0 ? " " : "disabled "}`} onClick={deletetext}>
        {" "}
        delete text
      </button>
      <button  className={`btn btn-primary mx-1 ${change.length > 0 ? " " : "disabled "}`} onClick={copytext}>
        {" "}
        copy text
      </button>
      <button  className={`btn btn-primary mx-1 ${change.length > 0 ? " " : "disabled "}`} onClick={extratext}>
        {" "}
        remove extra space
      </button>

      <div className="container my-3">
        <h3
          className={`text-capitalize text-${
            props.mode === "light" ? "dark" : "light"
          } mx-2`}
        >
          your text
        </h3>
        <p className={`text-${props.mode === "light" ? "dark" : "light"} mx-2`}>
          {" "}
          your word is {change.length} words and your charater is{" "}
          {change.split(" ").length}
        </p>
        <p className={`text-${props.mode === "light" ? "dark" : "light"} mx-2`}>
          {change.length>0?  0.008 * change.split(" ").length:"Enter something"} mitutes to read
        </p>
        <p className={`text-${props.mode === "light" ? "dark" : "light"} mx-2`}>
          {change.length > 0 ? change : "Enter something "}
        </p>
      </div>
    </div>
  );
}

export default Formtext;
