import { useState } from "react";
import "./App.css";

import Alert from "./Componenet/Alert";
import Formtext from "./Componenet/Formtext";
import Navbar from "./Componenet/Navbar";

function App() {
  const [mode, setmode] = useState("light");
  const [alert, setalert] = useState(null);
  const showAlert = (message, type) => {
    setalert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setalert(null);
    }, 1500);
  };
  const toggleHandler = () => {
    if (mode === "light") {
      setmode("dark");
      document.body.style.backgroundColor = "#042743";
      showAlert("dark mode have been enable", "success");
    } else if (mode === "dark") {
      setmode("light");
      document.body.style.backgroundColor = "white";
      showAlert("light mode have been enable", "success");
    }
  };

  return (
    <>
     <Navbar mode={mode} toggle={toggleHandler} />
            <Alert alert={alert} />
            <Formtext
              heading="Enter your text analyze below"
              mode={mode}
              showAlert={showAlert}
            />
    </>
  );
}

export default App;
