import React from "react";
import "../App.css";
import { NavLink } from "react-router-dom";

function Navbar(props) {
  return (
    <div>
      <div className={`navbar${props.mode}dark bg-${props.mode}`}>
        <header className="d-flex text-light flex-wrap justify-content-center py-3 mb-4">
          <a
            href="/"
            className="d-flex align-items-center mb-3 mb-md-0 me-md-auto text-dark text-decoration-none"
          >
            <svg className="bi me-2" width="40" height="32"></svg>
            <span className={`fs-4 text-${props.mode==="light"?"dark":"light"} `}>Text Utility</span>
          </a>

          <ul className="nav nav-pills">
            <li className="nav-item">
              <a href="/" className="nav-link " aria-current="page">
                Home
              </a>
            </li>
          
          
            

            <div class="form-check form-switch my-2">
              <input
                class="form-check-input"
                type="checkbox"
                id="flexSwitchCheckDefault"
                onChange={props.toggle}
              />
              <label class={`form-check-label text-${props.mode==="light"?"dark":"light"} mx-2` } for="flexSwitchCheckDefault">
              {props.mode==="light"?"dark mode":"light mode"}
                
              </label>
            </div>
           
          </ul>
        </header>
      </div>
    </div>
  );
}

export default Navbar;
